@import '../../styles/variables.scss';

.app__header {
    display: flex;
    justify-content: space-between;
}

.app__title {
    font-size: 28px;
    line-height: 37px;
    span {
        color: $main-color;
    }
}

.app__menu {
    &__list {
        display: flex;
        justify-content: space-between;
        font-weight: bold;
        font-size: 24px;
        line-height: 32px;
    }
    &__item {
        margin: 0 8px;
        a:hover {
            color: $main-color;
        }
    }
}